/*service*/
import request from "utils/request";
//
const URL = {
    "GET_ORDER_DETAIL":  `${GROBAL_HTTP_CTX}/purchase_order_detail/list`, // 
}

/**
 * 
 * @param {*} params
 */
export const getOrderDetail = (param) => {
    return request(URL.GET_ORDER_DETAIL, {
        method: "get",
        param
    });
}
