import { FormattedMessage, injectIntl } from 'react-intl';
import React, {Component} from 'react'
import {actions} from 'mirrorx';
import {Loading, Message} from 'tinper-bee';
import Grid from 'components/Grid';
import moment from 'moment'
import Header from 'components/Header';
import Button from 'components/Button';
import Alert from 'components/Alert';
import SearchArea from '../SearchArea/index';
import ButtonRoleGroup from 'components/ButtonRoleGroup';
import {BpmButtonSubmit, BpmButtonRecall} from 'yyuap-bpm';

import {deepClone, success, Error,getPageParam} from "utils";

import './index.less';

const format = "YYYY-MM-DD";

class IndexView extends Component {
    constructor(props) {
        super(props);
        this.state = {
            delModalVisible: false,
        }
    }

    componentDidMount() {
        this.loadPage();
    }

    /**
     *
     *
     * @param {Object} [searchParam={}]
     */
    loadPage = (searchParam = {}) => {
        //  
        const {pageSize, pageIndex} = this.props.orderObj;
        const tempPageSize = pageIndex ? pageIndex : 1; //
        const initPage = {pageIndex: tempPageSize - 1, pageSize};
        actions.masterDetailOne.loadList({...initPage, ...searchParam});
    }


    /**
     * btnFlag
     * @param {Number} btnFlag 012
     */
    onClickAddEditView = (btnFlag) => {
        const {selectIndex, orderObj} = this.props;
        const {list = []} = orderObj;
        let orderInfo = null;

        if (btnFlag !== 0) {
            orderInfo = list[selectIndex]
        }
        this.goToOrder(orderInfo, btnFlag);
    }

    /**
     *
     *URL
     * @param {string} id id
     * @param {Number} btnFlag  012
     */
    goToOrder = (orderInfo, btnFlag) => {
        actions.masterDetailOrder.setQueryParent(orderInfo)
        actions.routing.push(
            {
                pathname: 'order',
                search: `?search_id=${orderInfo ? orderInfo.id : ""}&btnFlag=${btnFlag}&t=${Math.random()}`
            }
        )
    }

    /**
     *
     * @param {Number} pageIndex  
     * @param {string} tableObj table
     */
    freshData = (pageIndex, tableObj) => {
        this.onPageSelect(pageIndex, 0, tableObj);
    }

    /**
     *
     *
     * @param {Number} pageIndex  
     * @param {Number} value 
     * @param {string} tableObj table
     */
    onDataNumSelect = (pageIndex, value, tableObj) => {
        this.onPageSelect(value, 1, tableObj);
    }

    /**
     *
     * @param {Number} value pageIndexpageSize
     * @param {Number} type type0 pageIndex,1 pageSize,
     * @param {string} tableName  table 
     */
    onPageSelect = (value, type, tableName) => {
        let searchParam = deepClone(this.props.searchParam); // action
        let modalObj = this.props[tableName];
        let {pageIndex, pageSize} = getPageParam(value, type,modalObj);

        if (tableName === "orderObj") { //
            searchParam.pageSize = pageSize;
            searchParam.pageIndex = pageIndex;
            actions.masterDetailOne.updateState({searchParam});
            actions.masterDetailOne.loadList(searchParam);
        }

        if (tableName === "detailObj") { //detail 
            const {selectIndex, orderObj} = this.props;
            const {id: search_orderId} = orderObj.list[selectIndex];
            const temp = {search_orderId, pageSize, pageIndex};
            actions.masterDetailOne.loadOrderDetailList(temp);
        }

    }

    /**
     *   
     */

    onClickDel = () => this.setState({delModalVisible: true});

    /**
     *
     * @param {Number} status  1 2 
     */
    async confirmGoBack(status) {
        const {selectIndex, orderObj} = this.props;
        const {list = []} = orderObj;
        const record = list[selectIndex];
        this.setState({delModalVisible: false});
        if (status === 1) { // 
            await actions.masterDetailOne.delOrder(record);
        }
    }

    /**
     * excel
     */
    export = () => {
        this.grid.exportExcel();
    }

    orderColumn = [
        {
            title: this.props.intl.formatMessage({id:"js.com.Ind6.0001", defaultMessage:""}),
            dataIndex: "orderCode",
            key: "orderCode",
            width: 250,
            render: (text, record, index) => {
                return (
                    <span
                        className="demo-table-code"
                        onClick={(event) => {
                            event.preventDefault();
                            this.goToOrder(record, 2);
                        }}>{text}</span>
                );
            }
        },
        {
            title: this.props.intl.formatMessage({id:"js.com.Ind6.0002", defaultMessage:"" }),
            dataIndex: "orderName",
            key: "orderName",
            width: 100,
        },
        {
            title: this.props.intl.formatMessage({id:"js.com.Ind6.0003", defaultMessage:""}),
            dataIndex: "orderTypeEnumValue",
            key: "orderTypeEnumValue",
            width: 100,

        },
        {
            title: this.props.intl.formatMessage({id:"js.com.Ind6.0004", defaultMessage:""}),
            dataIndex: "orderPrice",
            key: "orderPrice",
            width: 80,
            className: 'column-number-right ', // 
            render: (text, record, index) => {
                return (<span>{(typeof text)==='number'? text.toFixed(2):""}</span>)
            }
        },
        {
            title: this.props.intl.formatMessage({id:"js.com.Ind6.0005", defaultMessage:"" }),
            dataIndex: "orderUserName",
            key: "orderUserName",
            width: 200,
        },
        {
            title: this.props.intl.formatMessage({id:"js.com.Ind6.0006", defaultMessage:""}),
            dataIndex: "orderDeptName",
            key: "orderDeptName",
            width: 150,
        },
        {
            title: this.props.intl.formatMessage({id:"js.com.Ind6.0007", defaultMessage:""}),
            dataIndex: "orderDate",
            key: "orderDate",
            width: 150,
            render: (text, record, index) => {
                return <div>{text ? moment(text).format(format) : ""}</div>
            }
        },
        {
            title: this.props.intl.formatMessage({id:"js.com.Ind6.0008", defaultMessage:""}),
            dataIndex: "bpmStateEnumValue",
            key: "bpmStateEnumValue",
            width: 150,
        }
    ];

    detailColumn = [
        {
            title: <FormattedMessage id="js.com.Ind6.0009" defaultMessage="" />,
            dataIndex: "detailName",
            key: "detailName",
            width: 200,
            fixed: 'left',
        },
        {
            title: <FormattedMessage id="js.com.Ind6.0010" defaultMessage="" />,
            dataIndex: "detailModel",
            key: "detailModel",
            width: 200,
        },
        {
            title: <FormattedMessage id="js.com.Ind6.0011" defaultMessage="" />,
            dataIndex: "detailCount",
            key: "detailCount",
            width: 200,
            className: 'column-number-right ', // 
        },
        {
            title: <FormattedMessage id="js.com.Ind6.0012" defaultMessage="" />,
            dataIndex: "detailDate",
            key: "detailDate",
            width: 200,
            render: (text, record, index) => {
                return <div>{text ? moment(text).format(format) : ""}</div>
            }
        },

    ];


    /**
     *
     * 
     * @param {Object} data 
     * @returns
     */
    getBasicPage = (data) => {
        const {pageIndex, total, totalPages} = data;
        return {   // 
            activePage: pageIndex,//
            total: total,//
            items: totalPages,
            dataNum: 1, //
        };

    }

    /**
     *
     * @description 
     * @param {string, string} operationsubmit recall type startsuccess
     */
    bpmStart = (operation, type) => async () => {
        const { intl } = this.props;
        if (type == 'start') {
            await actions.masterDetailOne.updateState({
                showLoading: true
            })
        } else {
            let msg = operation == 'submit' && intl.formatMessage({id:"js.com.Ind6.0013", defaultMessage:""}) || intl.formatMessage({id:"js.com.Ind6.0014", defaultMessage:""});
            success(msg);
            const searchParam = deepClone(this.props.searchParam); // action
            this.loadPage(searchParam);
        }

    }

    /**
     *
     * @description 
     * @param {string,string} operationsubmit recall type errorend
     */
    bpmEnd = (operation, type) => async (error) => {
        if (type == 'error') {
            Error(error.msg);
        }
        actions.masterDetailOne.updateState({
            showLoading: false
        })
    }


    getDataNum = (num) => {
        const val = {"5": 0, "10": 1, "15": 2, "20": 3, "25": 4, "50": 5};
        let dataNum = val[num];
        if (dataNum === undefined) {
            dataNum = 6;
        }
        return dataNum;
    }

    handleBpmState = (list, selectIndex) => {
        let resObj = {
            submitForbid : true,
            recallForbid : true
        };
        if ( list.length ) {
            let bpmState = list[selectIndex]['bpmState'];
            let submitForbid = bpmState ? true : false,
                recallForbid = bpmState == 1 ? false : true;

            resObj = {
                submitForbid,
                recallForbid
            }
        }

        return resObj;
    }


    render() {
        const _this = this;
        const {delModalVisible} = _this.state;
        let {showLoading, showDetailLoading, orderObj, detailObj, selectIndex} = this.props;
        let {list} = orderObj;

        const {submitForbid, recallForbid} = _this.handleBpmState(list, selectIndex);
        //  disable
        const btnForbid = list.length === 0;
        return (
            <div className="master-detail-one">
                <Header title={this.props.intl.formatMessage({id:"js.com.Ind6.0015", defaultMessage:"B2 "})}/>
                <SearchArea orderObj={orderObj}/>
                <div className='table-header'>
                    <ButtonRoleGroup funcCode="masterdetail-one">
                        <Button
                            colors="primary"
                            className="ml8"
                            role="add"
                            onClick={() => _this.onClickAddEditView(0)}
                        ><FormattedMessage id="js.com.Ind6.0016" defaultMessage="" /></Button>
                        <Button
                            shape="border"
                            className="ml8"
                            role="update"
                            disabled={submitForbid || btnForbid }
                            onClick={() => _this.onClickAddEditView(1)}
                        ><FormattedMessage id="js.com.Ind6.0017" defaultMessage="" /></Button>
                        <Button
                            shape="border"
                            className="ml8"
                            disabled={btnForbid}
                            onClick={() => _this.onClickAddEditView(2)}
                        ><FormattedMessage id="js.com.Ind6.0018" defaultMessage="" /></Button>
                        <Button
                            role="delete"
                            shape="border"
                            className="ml8"
                            disabled={submitForbid || btnForbid}
                            onClick={_this.onClickDel}
                        ><FormattedMessage id="js.com.Ind6.0019" defaultMessage="" /></Button>
                        <Alert show={delModalVisible} context=" ?"
                               confirmFn={() => _this.confirmGoBack(1)}
                               cancelFn={() => _this.confirmGoBack(2)}
                        />
                        <Button
                          shape="border"
                          key="export"
                          className="ml8"
                          disabled={btnForbid}
                          onClick={_this.export}
                        >
                            <FormattedMessage id="js.com.Ind6.0021" defaultMessage="" />
                        </Button>
                        <BpmButtonSubmit
                            className="ml8"
                            checkedArray={[orderObj['list'][selectIndex]]}
                            funccode="masterdetail-one"
                            nodekey="purchaseOrder"
                            url={`${GROBAL_HTTP_CTX}/purchase_order/submit`}
                            urlAssignSubmit={`${GROBAL_HTTP_CTX}/purchase_order/assignSubmit`}
                            onStart={_this.bpmStart('submit', 'start')}
                            onSuccess={_this.bpmStart('submit', 'success')}
                            onError={_this.bpmEnd('submit', 'error')}
                            onEnd={_this.bpmEnd('submit', 'end')}
                        >
                            <Button className="ml8"  size='sm' colors="primary"
                                disabled={submitForbid}><FormattedMessage id="js.com.Ind6.0022" defaultMessage="" /></Button>
                        </BpmButtonSubmit>
                        <BpmButtonRecall
                            checkedArray={[orderObj['list'][selectIndex]]}
                            url={`${GROBAL_HTTP_CTX}/purchase_order/recall`}
                            onStart={_this.bpmStart('recall', 'start')}
                            onSuccess={_this.bpmStart('recall', 'success')}
                            onError={_this.bpmEnd('recall', 'error')}
                            onEnd={_this.bpmEnd('recall', 'end')}
                        >
                            <Button
                              className="ml8"
                              size='sm'
                              colors="primary"
                              disabled={recallForbid}><FormattedMessage id="js.com.Ind6.0023" defaultMessage="" /></Button>
                        </BpmButtonRecall>
                    </ButtonRoleGroup>
                </div>
                <Grid
                    ref={(el) => this.grid = el}
                    data={orderObj.list}
                    rowKey={(r, i) => i}
                    columns={this.orderColumn}
                    multiSelect={false}
                    dragborder={true}
                    onRowClick={(record, index) => {
                        // 
                        actions.masterDetailOne.updateState({selectIndex: index}); //  
                        const {list} = orderObj;
                        const {pageSize} = detailObj;
                        const {id: search_orderId} = list[index];
                        const param = {search_orderId, pageSize, pageIndex: 0};
                        actions.masterDetailOne.loadOrderDetailList(param);
                    }}
                    rowClassName={(record, index, indent) => {
                        return selectIndex === index ? "selected" : "";
                    }}

                    // 
                    paginationObj={{
                        ...this.getBasicPage(orderObj),
                        freshData: pageSize => _this.freshData(pageSize, "orderObj"),
                        onDataNumSelect: (index, value) => _this.onDataNumSelect(index, value, "orderObj"),
                        dataNum: _this.getDataNum(orderObj.pageSize),
                    }}
                />
                <div className="table-space"> </div>
                <div className='gird-parent'>
                    <Grid
                        data={detailObj.list}
                        rowKey={(r, i) => i}
                        columns={this.detailColumn}
                        multiSelect={false}
                        // 
                        paginationObj={{
                            ...this.getBasicPage(detailObj),
                            freshData: pageSize => _this.freshData(pageSize, "detailObj"),
                            onDataNumSelect: (index, value) => _this.onDataNumSelect(index, value, "detailObj"),
                            dataNum: _this.getDataNum(detailObj.pageSize),
                        }}
                        loading={{show: (!showLoading && showDetailLoading)}}
                    />
                    <Loading
                        show={showLoading}
                        fullScreen={true}
                    />
                </div>
            </div>

        )

    }
}
export default injectIntl(IndexView)