import { FormattedMessage, injectIntl } from 'react-intl';
import React, {Component} from 'react'
import {actions} from "mirrorx";
import {FormControl, Select} from "tinper-bee";
import FormList from 'components/FormList';
import SearchPanel from 'components/SearchPanel';

import './index.less'

const FormItem = FormList.Item;
const {Option} = Select;

class SearchArea extends Component {
    constructor(props) {
        super(props)
        this.state = {}
    }

    /** 
     * @param {*} error 
     * @param {Object} values 
     */
    search = () => {
        this.props.form.validateFields((err, values) => {
            //  
            if(!err){
                const {pageSize} = this.props.orderObj;
                values.pageIndex = 0;
                values.pageSize = pageSize;
                actions.masterDetailOne.loadList(values);
            }
        });
    }

    reset = () => {
        this.props.form.resetFields();
    }

    render() {
        const {form: {getFieldProps}} = this.props;
        return (
            <SearchPanel
                reset={this.reset}
                search={this.search}
                intl={this.props.intl}
            >

                <FormList size="sm">
                    <FormItem
                        label={<FormattedMessage id="js.com.Sea4.0001" defaultMessage="" />}
                    >
                        <FormControl
                            placeholder={this.props.intl.formatMessage({id:"js.com.Sea4.0002", defaultMessage:""})}
                            {...getFieldProps('search_orderCode', {initialValue: '',})}
                        />
                    </FormItem>

                    <FormItem
                        label={<FormattedMessage id="js.com.Sea4.0003" defaultMessage="" />}
                    >
                        <FormControl placeholder={this.props.intl.formatMessage({id:"js.com.Sea4.0002", defaultMessage:""})} {...getFieldProps('search_orderName', {initialValue: '',})}/>
                    </FormItem>

                    <FormItem
                        label={<FormattedMessage id="js.com.Sea4.0004" defaultMessage="" />}
                    >
                        <Select {...getFieldProps('search_orderType', {initialValue: ''})}>
                            <Option value=""><FormattedMessage id="js.com.Sea4.0005" defaultMessage="" /></Option>
                            <Option value="1"><FormattedMessage id="js.com.Sea4.0006" defaultMessage="" /></Option>
                            <Option value="2"><FormattedMessage id="js.com.Sea4.0007" defaultMessage="" /></Option>
                            <Option value="3"><FormattedMessage id="js.com.Sea4.0008" defaultMessage="" /></Option>
                        </Select>
                    </FormItem>

                    <FormItem
                        label={<FormattedMessage id="js.com.Sea4.0009" defaultMessage="" />}
                    >
                        <Select {...getFieldProps('search_bpmState', {initialValue: ''})}>
                            <Option value=""><FormattedMessage id="js.com.Sea4.0010" defaultMessage="" /></Option>
                            <Option value={0}><FormattedMessage id="js.com.Sea4.0011" defaultMessage="" /></Option>
                            <Option value={1}><FormattedMessage id="js.com.Sea4.0012" defaultMessage="" /></Option>
                            <Option value={2}><FormattedMessage id="js.com.Sea4.0013" defaultMessage="" /></Option>
                            <Option value={3}><FormattedMessage id="js.com.Sea4.0014" defaultMessage="" /></Option>
                        </Select>
                    </FormItem>
                </FormList>

            </SearchPanel>
        )
    }
}

export default FormList.createForm()(injectIntl(SearchArea))
