import request from "utils/request";
//
const URL = {

    "DEL_ORDER_DETAIL":  `${GROBAL_HTTP_CTX}/purchase_order_detail/deleteBatch`, // 
    "SAVE_ASSO":  `${GROBAL_HTTP_CTX}/purchase_order/saveAssoVo`, //
    "GET_USER": `${GROBAL_HTTP_CTX}/purchase_order/listForAdd`, //

}


/**
 * 
 * @param {*} params
 */
export const saveAsso = (params) => {
    return request(URL.SAVE_ASSO, {
        method: "post",
        data:params
    });
}

/**
 * 
 * @param {*} params
 */
export const delOrderDetail = (params) => {
    return request(URL.DEL_ORDER_DETAIL, {
        method: "post",
        data:params
    });
}


/**
 * 
 * @param {*} params
 */
export const getUser = (param) => {
    return request(URL.GET_USER, {
        method: "get",
        param
    });
}

