import request from "utils/request";
//
const URL = {
    "GET_LIST":  `${GROBAL_HTTP_CTX}/purchase_order/list`, // 
    "GET_ORDER_DETAIL":  `${GROBAL_HTTP_CTX}/purchase_order_detail/list`, // 
    "DEL_ORDER":  `${GROBAL_HTTP_CTX}/purchase_order/deleAssoVo`, // 
    "DEL_ORDER_DETAIL":  `${GROBAL_HTTP_CTX}/purchase_order_detail/deleteBatch`, // 
    "SAVE_ASSO":  `${GROBAL_HTTP_CTX}/purchase_order/saveAssoVo`, //
    "GET_USER": `${GROBAL_HTTP_CTX}/purchase_order/listForAdd`, //

}

/**
 * 
 * @param {*} params
 */
export const getList = (param) => {
    return request(URL.GET_LIST, {
        method: "get",
        param
    });
}

/**
 * 
 * @param {*} params
 */
export const getOrderDetail = (param) => {
    return request(URL.GET_ORDER_DETAIL, {
        method: "get",
        param
    });
}


/**
 * 
 * @param {*} params
 */
export const delOrder = (params) => {
    return request(URL.DEL_ORDER, {
        method: "post",
        data:params
    });
}



/**
 * 
 * @param {*} params
 */
export const getUser = (param) => {
    return request(URL.GET_USER, {
        method: "get",
        param
    });
}

